const poplink = document.getElementById('popup-link');
const popClose = document.getElementById('popup-close');
const fot = document.querySelector('.footer');
const ModFot = document.querySelector('.footer-modal')
const FotCont = document.querySelector('.footer-pedcont');

FotCont.classList.remove('footer-cont');

poplink.addEventListener('click',function(){
    fot.classList.add('footer-modal');
    fot.classList.remove('footer');
    popClose.classList.remove('none');
    FotCont.classList.add('footer-cont');
    ModFot.classList.add('footer-cont');
})

popClose.addEventListener('click',function(){
    fot.classList.remove('footer-modal');
    fot.classList.add('footer');
    popClose.classList.add('none');
    FotCont.classList.remove('footer-cont');
    
})